form {
  display: flex;
  justify-content: center;
}

fieldset {
  border-radius: 10px;
  border-color: #495e57;
  display: flex;
  flex-direction: column;

}

fieldset div {
  display: flex;
  flex-direction: row;
}

fieldset div,
input {
  margin: 1%;
}

fieldset div label,
.error-message,
.res-form-h2 {
  margin-right: 10px;
  margin-left: 10px;
}

.res-btn {
  background-color: #F4CE14;
  border: none;
  font-weight: bold;
  border-radius: 20px;
  font-size: 15px;
  color: black;
  padding: 7px 9px;
  margin-left: 10px;
  margin-bottom: 10px;
}

.website {
  display: flex;
  justify-content: center;

  align-items: center;
  border: solid #495e57;

  border-radius: 20px;
  padding: 1%;
}

.website h2 {
  font-size: 30px;
}

.error-message {
  color: red;
  font-size: small;
}

.res-form-h2 {
  font-size: medium;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.navbar-div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.links{
  margin-left: 2rem;
margin-top: 1rem;
align-items: center;
}
.burger {
  display: none;
}

